import TicketService from "../services/TicketService";
import React from "react";
import { Button } from "semantic-ui-react";

class Tickets extends React.Component {
  state = {
    tickets: [],
  };
  getTimeForPrize(){
    const date = new Date(2022, 1, 14, 22, 30, 0, 0);
    const now = new Date();
    const diff = date.getTime() - now.getTime();
    const days = Math.floor(diff / (1000 * 60 * 60 * 24));
    const hours = Math.floor((diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    const minutes = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
    const seconds = Math.floor((diff % (1000 * 60)) / 1000);
    return `${days} días, ${hours} horas, ${minutes} minutos y ${seconds} segundos`;
  }
  componentDidMount() {
    const loadTickets = () => {
      TicketService.getAll().then((tickets) => {
        this.setState({
          tickets: tickets.data,
        });
      });
    };
    loadTickets();
  }
  render() {
    return (
      <div className="ui grid">
        <div className="row">
          <div className="ui three wide column">
            <div className="ui cards">
              <div className="card">
                <div className="content">
                  <div className="header">Sorteo</div>
                  <div className="meta">Consigue tu boleta</div>
                  <div className="description">
                  <p>Sorteo de una botella de Vodka Smirnoff de Lulo 750 mL. Se jugará el día lunes 14 de febrero de 2021 con la lotería de Cundinamarca</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="ui ten wide column">
            <div className="ui blue centered segment">
              <h1>Boletas</h1>
              <p>Las azules son las disponibles y las rojas fueron tomadas</p>
            </div>
          </div>
          <div className="ui three wide column">
          <div className="ui cards">
              <div className="card">
                <div className="content">
                  <div className="header">Sorteo</div>
                  <div className="meta">Aquí se publicará al ganador</div>
                  <div className="description">
                  <p>Quedan {this.getTimeForPrize()} para el sorteo</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="ui three wide column"></div>
          <div className="ten wide column">
            <div className="ui stackable segment">
              {this.state.tickets.map((ticket, index) =>
                ticket.choosen ? (
                  <Button color={"red"}>
                    {ticket.id - 1 < 10
                      ? "00" + (ticket.id - 1)
                      : ticket.id - 1 < 100
                      ? "0" + (ticket.id - 1)
                      : ticket.id - 1}
                  </Button>
                ) : (
                  <Button primary>
                    {ticket.id - 1 < 10
                      ? "00" + (ticket.id - 1)
                      : ticket.id - 1 < 100
                      ? "0" + (ticket.id - 1)
                      : ticket.id - 1}
                  </Button>
                )
              )}
            </div>
          </div>
          <div className="ui three wide column"></div>
        </div>
      </div>
    );
  }
}

export default Tickets;
